import React, { useContext, useEffect, useState } from 'react'
import style from './Cart.module.css'
import { CartContext } from '../../Context/CartContext'
import { Puff } from 'react-loader-spinner';
import axios from 'axios';
import Products from '../Products/Products';
import { Link } from 'react-router-dom';

export default function Cart() {

  let {getCartItems , deleteCartItems , updateCartItems , clearCart , setCounter} = useContext(CartContext);
  const [cart, setCart] = useState({})
  const [loading, setLoading] = useState(true)

  async function getItems(){
    try {
      setLoading(true)
    let {data} = await getCartItems();
    setCart(data)
    
    
    setLoading(false)
    } catch (error) {
      console.log(error);
      setLoading(false)
      
    }

  }

  async function deleteItem(id){
    setLoading(true);
    let {data} = await deleteCartItems(id);
    setCart(data)
    setLoading(false)

  }

  async function deleteCart(){
    setLoading(true);
    let {data} = await clearCart();
    setCart(data)
    setLoading(false)

  }

  

  async function updateCart(id , count){
    setLoading(true);
    if (count < 1 ) {
      let {data} = await deleteCartItems(id);
      setCart(data);
    }else {
      let {data} = await updateCartItems(id , count);
      setCart(data)
      setLoading(false)
    }
    
    

  }

  useEffect(()=>{
    getItems()
    
  } , [])

  return <>

{loading? 
  <div className="row justify-content-center align-items-center vh-100">
    <Puff
  visible={true}
  height="100"
  width="100"
  color="#9BABB8"
  ariaLabel="puff-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content-center"
  />
  </div> : cart?
  
  <>
  {cart.data?.products.length > 0 ? cart? <div className='my-5'>
        <button onClick={deleteCart} className='btn btn-outline-danger d-block ms-auto'>Clear Cart</button>

        {cart.data?.products.map((product , index) => <div key={index} className="row align-items-center border-bottom border-1 p-2 m-0">
    <div className="col-md-2">
      <div className="img">
        <img src={product.product.imageCover} className='w-100' alt={product.product.title} />
      </div>
    </div>
    <div className="col-md-10 d-flex justify-content-between">
      <div className="item">
        <h3 className='h5 fw-bold'>{product.product.title.split(' ').splice(0,3).join(' ')}</h3>
        <h6 className='text-main fw-bold'> Price : {product.price} EGP </h6>
        <button onClick={()=> deleteItem(product.product.id)} className='btn btn-sm m-0 p-0'><i className='fas fa-trash-can text-danger'></i> Remove </button>
      </div>
      <div className="count">
        <button onClick={()=> updateCart(product.product.id , product.count +1)} className='btn btn-md brdr btn-outline-secondary'>+</button>
        <span className='mx-3 '>{product.count}</span>
        <button onClick={()=> updateCart(product.product.id , product.count -1)} className='btn brdr btn-outline-secondary'>-</button>
      </div>
    </div>
  </div>)}

        <div className='d-flex justify-content-between'>
          <Link to={`/shippingaddress/${cart.data._id}`} className='btn bg-main text-light'> Online Payment </Link>
          <p>Total cart Price: {cart.data?.totalCartPrice} EGP</p>
        </div>

      </div>:
            <div className=" align-items-center justify-content-center">
            <h2 className='alert alert-warning text-center my-5 '>No products in your cart</h2>
            <div className="empty"></div>
          </div> 
  
  
  
  
  
  
  
            : <div className=" align-items-center justify-content-center">
            <h2 className='alert alert-warning text-center my-5 '>No products in your cart</h2>
            <div className="empty"></div>
          </div> }
  
  </>
  
  : <div className=" align-items-center justify-content-center">
    <h2 className='alert alert-warning text-center my-5 '>No products in your cart</h2>
    <div className="empty"></div>
  </div> }
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  
  </>
}
