import React, { useEffect, useState } from 'react'
import style from './AllOrders.module.css'
import { jwtDecode } from 'jwt-decode';
import axios from 'axios';
import { Puff } from 'react-loader-spinner';

export default function AllOrders() {

  let [orders , setOrders] = useState([])
  const [loading, setLoading] = useState(true)

  


  async function getAllOrders(id){
    let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/orders/user/${id}`)
    setOrders(data);
  }

  useEffect(()=>{
    let {id} = jwtDecode(localStorage.getItem('userToken'));
    getAllOrders(id)
    setLoading(false)
  } , [])
  return <>
  
  <h2 className='text-center text-second mt-5'>All Orders</h2>
  <hr />

  {loading? 
  <div className="row justify-content-center align-items-center vh-100">
    <Puff
  visible={true}
  height="100"
  width="100"
  color="#9BABB8"
  ariaLabel="puff-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content-center"
  />
  </div> :
  <>
  {orders.map((order) => {
    return <div key={order.id} className="row">
    <div className="rounded p-4 my-5">
      <div className="d-flex align-items-center">
        <h3 className='h1 fw-bolder'>#{order.id}</h3>
        <h4 className='fw-bold text-primary mx-4'>Processing</h4>
      </div>
      <p>You have orderd {order.cartItems.length} items</p>
      <div className="d-flex">
        {order.cartItems.map((item) => {
          return <img src={item.product.imageCover} className='w-100' key={item._id} alt="" />
        })}
      </div>
      <hr />
      <p><strong>Total Amount :</strong> {order.totalOrderPrice} LE</p>
    </div>
  </div>
  })}
  
  </>
  
  }

  

  
  </>
}
